  background-color: #f5f5f5;
  text-align: center; }
  .footer a {
    text-decoration: none;
    color: inherit;
    border-bottom: 1px solid; }
    .footer a:hover {
      border-bottom: 1px transparent; }

.hide {
  text-indent: -999999px;
  font-size: 0; }

.postage {
  width: 100%;
  min-width: 250px;
  display: block;
  height: 50px;
  position: relative;
  border-radius: 5px;
  background: linear-gradient(to right, #ffcf3e 35%, #ffe79d 100%);
  margin-bottom: 20px;
  padding: 15px 25px 0 40px;
  color: darkslategray;
  box-shadow: 1px 2px 1px -1px #777;
  transition: background 200ms ease-in-out; }

.shadow {
  position: relative; }
  .shadow:before {
    z-index: -1;
    position: absolute;
    content: "";
    bottom: 13px;
    right: 7px;
    width: 75%;
    top: 0;
    box-shadow: 0 15px 10px #777;
    transform: rotate(4deg);
    transition: all 150ms ease-in-out; }

.box:hover {
  background: linear-gradient(to right, #ffcf3e 0%, #ffcf3e 100%); }

.shadow:hover::before {
  transform: rotate(0deg);
  bottom: 20px;
  z-index: -10; }

.circle {
  position: absolute;
  top: 14px;
  left: 15px;
  border-radius: 50%;
  box-shadow: inset 1px 1px 1px 0px rgba(0, 0, 0, 0.5), inset 0 0 0 25px antiquewhite;
  width: 20px;
  height: 20px;
  display: inline-block; }

.avatar {
  min-width: 50px;
  height: 50px;
  border-radius: 50%;
  align-self: center; }

.card {
  background: #ffffff;
  padding: 5rem;
  box-shadow: 0 1px 10px rgba(0, 0, 0, 0.15); }

pre.error {
  padding: 5rem;
  background: #ffffff;
  line-height: 3;
  white-space: pre-line; }

.grit {
  background: url("/images/photos/blackgrit.png");
  position: relative; }
  .grit:hover {
    animation: gritty 2s steps(10) infinite; }

@keyframes gritty {
  from {
    background-position: 0; }
  to {
    background-position: -600px; } }

@keyframes moveInLeft {
  0% {
    opacity: 0;
    transform: translate(-30rem); }
  80% {
    transform: translate(1rem); }
  100% {
    opacity: 1;
    transform: translate(0); } }

@keyframes moveInRight {
  0% {
    opacity: 0;
    transform: translate(30rem); }
  80% {
    transform: translate(-1rem); }
  100% {
    opacity: 1;
    transform: translate(0); } }

@keyframes moveInButton {
  0% {
    opacity: 0;
    transform: translateY(3rem); }
  100% {
    opacity: 1;
    transform: translate(0); } }

/*
0 - 600PX: PHONE
600 - 900PX: TABLET PORTRAIT
900 - 1200PX: TABLET LANDSCAPE
[1200 - 1800] IS WHERE OUR NOMAL STYLES APPLY
1800PX + : BIG DESKTOP

$breakpoint arguement choices:
- phone
- tab-port
- tab-land
- big-desktop

ORDER: Base + typography > general layout + grid > page layout > components

1em = 16px
*/
.tags {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  margin-bottom: 2rem;
  flex-wrap: wrap; }

.verticalTags {
  margin: 1rem;
  padding: 1rem;
  list-style: none; }

.tag {
  display: block;
  margin-right: 1rem; }
  .tag__choice {
    display: flex;
    justify-content: center;
    align-items: center; }
    .tag__choice input {
      width: auto;
      right: -1.5rem;
      margin-right: -1rem;
      position: relative;
      z-index: 2; }
      .tag__choice input + label {
        background: #e6e6e6;
        padding: 1rem 1rem 1rem 4rem; }
      .tag__choice input:checked + label {
        background: #ffcf3e; }
  .tag__link {
